@import "../../includes/settings.scss";

.about-container {
    display: flex;
    max-width: $maxWidth;
    flex: 1 1 100%;
    margin: 0 auto;
    padding: 80px 150px;
    flex-direction: column;
    align-items: center;

    h1 {
        color: $primary;
        font-family: sans-serif;
        font-size: 48px;
    }

    .content {
        max-width: 1000px;

        p {
            text-align: center;
            font-family: sans-serif;
            font-size: 18px;
        }
    }
}

@media only screen and (max-width: $desktop) {
    .about-container {
        padding: 40px $mobileGutter;

        h1 {
            font-size: 36px;
        }

        .content {
            p {
                font-size: 16px;
            }
        }
    }
}